export function osIsPc() {
    var userAgentInfo = navigator.userAgent;
    var agents = ['Android', 'iPhone', 'SymbianOS', 'Windows Phone', 'iPod', 'iPad'];
    var flag = true;
    for (var i = 0; i < agents.length; i++) {
        if (userAgentInfo.indexOf(agents[i]) > 0) {
            flag = false;
            break;
        }
    }
    return flag;
}
export function isWifi() {
    var navigator = window.navigator;
    var userAgentInfo = navigator.userAgent.toLowerCase();
    var index = userAgentInfo.indexOf('wifi');
    if (index > -1) {
        return true;
    }
    var connection = navigator.connection || navigator.mozConnection || navigator.webkitConnection;
    var connectionType = connection && connection.type;
    if (connectionType === 'wifi') {
        return true;
    }
    return false;
}
export function randomString(len) {
    if (len === void 0) { len = 32; }
    var chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    var maxPos = chars.length;
    var pwd = '';
    for (var i = 0; i < len; i++) {
        pwd += chars.charAt(Math.floor(Math.random() * maxPos));
    }
    return pwd;
}
export function dateFormat(obj, fmt) {
    var o = {
        "M+": obj.getMonth() + 1,
        "d+": obj.getDate(),
        "h+": obj.getHours(),
        "m+": obj.getMinutes(),
        "s+": obj.getSeconds(),
        "q+": Math.floor((obj.getMonth() + 3) / 3),
        "S+": obj.getMilliseconds() //毫秒 
    };
    if (/(y+)/.test(fmt)) {
        fmt = fmt.replace(RegExp.$1, (obj.getFullYear() + "").substr(4 - RegExp.$1.length));
    }
    for (var k in o) {
        if (new RegExp("(" + k + ")").test(fmt)) {
            fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : ((((RegExp.$1.length == 3 && k == "S+") ? "000" : "00") + o[k]).substr(("" + o[k]).length)));
        }
    }
    return fmt;
}
